.header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    &__container {
        text-align: center;
        height: 100%;
        position: relative;
    }
    // Hedaer Socials
    &__socials {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.8rem;
        position: absolute;
        left: 0;
        bottom: -23rem;
        &::after {
            content: '';
            width: 1px;
            height: 2rem;
            background: var(--color-primary);
        }
    }
}
// CTA
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
// ME
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 20.5rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
// SCROLL DOWN
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: -20rem;
    transform: rotate(90deg);
    font-weight: 100;
    font-size: 0.9rem;
}
// MEDIA QUERIES (MEDIUM DEVICES)
@media (max-width: 1024px) {
    .header {
        height: 68vh;
    }
    .me {
        height: 25rem;
    }
}
// MEDIA QUERIES (SMALL DEVICES)
@media (max-width: 600px) {
    .header {
        height: 100vh;
        &__socials {
            display: none;
        }
    }
    .scroll__down {
        display: none;
    }
    .me {
        height: 26.5rem;
    }
}