.testimonials {
    &__container {
        width: 40%;
        padding-bottom: 4rem;
    }
}
.testimonial {
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
    .client {
        &__avatar {
            width: 4rem;
            aspect-ratio: 1/1;
            overflow: hidden;
            border-radius: 50%;
            margin: 0 auto 1rem;
            border: 0.4rem solid var(--color-primary-variant);
        }
        &__review {
            color: var(--color-ligth);
            font-weight: 300;
            display: block;
            width: 80%;
            margin: 0.8rem auto 0;
        }
    }
}
.swiper-pagination {
    &-clickable,
    &-bullet {
        color: var(--color-primary);
        margin: 14rem 14rem;
    }
}
// MEDIA QUERIES (MEDIUM DEVICES)
@media (max-width: 1024px) {
    .testimonials {
        &__container {
            width: 60%;
        }
    }
    .swiper-pagination {
        &-clickable,
        &-bullet {
            display: none;
        }
    }
}
// MEDIA QUERIES (SMALL DEVICES)
@media (max-width: 600px) {
    .testimonials {
        &__container {
            width: var(--container-width-sm);
        }
    }
    .client {
        &__review {
            width: var(--container-width-sm);
        }
    }
    .swiper-pagination {
        &-clickable,
        &-bullet {
            display: none;
        }
    }
}